              src: "django-contact.png",
              set: 6,
            },
          ],
        },
        {
          id: 7,
          name: "API Open Libra Library",
          dialog: false,
          color: "red darken-4",
          stack: [
            "HTML-CSS-Javascript",
            "Responsive Web Design",
            "Javascript - Fetch API",
            "Dynamic Searches by title, author, subject and category",
          ],
          images: [
            {
              title: "Home Page",
              src: "biblioteca1.png",
              set: 7,
            },
            {
