              src: "biblioteca3.png",
              set: 7,
            },
          ],
        },
      ],
      page: 1,
      pageSize: 2,
      listCount: 0,
      paginatedList: [],
    };
  },

  created() {
    this.initPage();
    this.updatePage(this.page);
  },
  methods: {
    initPage: function () {
      this.listCount = this.projects.length;
      if (this.listCount < this.pageSize) {
        this.paginatedList = this.projects;
      } else {
        this.paginatedList = this.projects.slice(0, this.pageSize);
      }
    },
    updatePage: function (pageIndex) {
      let start = (pageIndex - 1) * this.pageSize;
      let end = pageIndex * this.pageSize;
      this.paginatedList = this.projects.slice(start, end);
      this.page = pageIndex;
      window.scrollTo(0, 0);
    },
  },
  computed: {
    pages() {
      if (this.pageSize == null || this.listCount == null) return 0;
      return Math.ceil(this.listCount / this.pageSize);
    },
  },
};
</script>

<style>
@import "https://cdnjs.cloudflare.com/ajax/libs/lightbox2/2.11.3/css/lightbox.min.css";

.v-pagination__item {
  width: 60px !important;
  height: 60px !important;
}

.v-pagination__navigation {
  width: 60px !important;
  height: 60px !important;
}

.button {
  width: 400px;
  font-size: 20px !important;
  padding: 30px !important;
}
.title-component {
  text-shadow: 4px 4px 8px #4e4e4e;
  margin-bottom: 30px;
  color: rgba(0, 0, 0, 0.87);
  font-weight: 300;
  font-family: Roboto, sans-serif !important;
  font-size: 54px;
}
.card-title {
  color: white;
  text-shadow: 2px 2px 2px black;
  letter-spacing: 2px;
  font-size: 36px !important;
}
.v-image .v-image__image {
	 transition: all 0.2s;
}
 .v-image.zoom .v-image__image {
	 transform: scale(0.9);
}

@media (max-width: 600px) {
  .button {
    width: 200px;
  }
  .v-pagination__item {
    width: 32px !important;
    height: 32px !important;
  }

  .v-pagination__navigation {
    width: 32px !important;
    height: 32px !important;
  }
}

@media (max-width: 470px) {
  .card-title {
    font-size: 20px !important;
  }
}

@media (max-width: 360px) {
  .button {
    font-size: 10px !important;
  }
}

@media (max-width: 300px) {
  .title-component {
    font-size: 30px !important;
  }
  .button {
    width: 100px;
  }
  .card-title {
    font-size: 12px !important;
  }
}

@media (max-width: 270px) {
  .button {
    font-size: 8px !important;
  }
}
</style>